import HeaderBack from '../Common/HeaderBack'
import Order from '../Common/Order'

export default {
  components: {
    myHeader: HeaderBack,
    myOrder: Order,
  },
  data () {
    return {
      header: {
        HBL_icon:"icon_back",
        HBC_text:"订单详情",
        HBR_icon: "",
        HBR_show: false,
      },
      orderDetail: {
        id: "",
        type: 0,// type 0:线上支付(微信端) 1:toB端
        createTime: "",
        modifyTime: "",
        status: 0,// status 0:待付款 1:待收货 25:待评价 9:交易完成 30:退款/售后
        productList: [],
        payableFreight: "",
        allAccount: 0,
        productDiscountAmount: 0,
        disAccount: 0,
        manjianAmount: 0.00,
        lastAccount: 0,
        payAccount: 0,
        address: {}
      }
    }
  },
  methods: {
    // 取消订单
    sendCancelFn () {
      const _this = this;
      this.$vux.confirm.show({
        // content: '确定取消当前订单吗？',
        title: '确定取消当前订单吗？',
        onConfirm () {
          let orderId = _this.id;
          // 开始动画
          comTools.showLoading(_this,'取消订单中...');
          setTimeout((data) => {
            // 结束动画
            comTools.hideLoading(_this);
            if(true){
              _this.$vux.toast.show({
                type:'success',
                text:'取消成功',
                width: '7.5em'
              });
            }
          }, 500)
        }
      });
    },
    // 支付订单
    sendPayFn () {
      let orderId = this.id;
      // 数据加载
      comTools.showLoading(this,'支付中...');
      setTimeout((data) => {
        // 数据加载结束动画
        comTools.hideLoading(this);
        // 走支付流程
        this.weixinpay();
      }, 500)
    },
    weixinpay(data) {
      let orderId = this.orderDetail.id;
      if (Math.random()*10 > 3) {
        this.$router.push({path:'/payMent/success/' + orderId });
      } else {
        this.$router.push({path:'/payMent/fail/' + orderId });
      }
    },
    // 查看物流
    sendLogistics() {
      // 物流详情页面跳转
      let orderId = this.$route.params.orderID;
      let value = {
        orderId: orderId,
        productList: this.orderDetail.productList
      }
      this.$store.commit('setData', { key: 'orderObj', value: value });
      this.$router.push({ path: '/mainpage/self/logistics' });
    },
    // 确认收货
    sendReceiptFn () {
      const _this = this;
      this.$vux.confirm.show({
        // content: '确定收到检测用品吗？',
        title: '确定收到检测用品吗？',
        onConfirm() {
          // 确定
          let orderId = _this.id;
          let value = {
            orderId: orderId,
            productList: _this.productList
          }
          // 开始动画
          comTools.showLoading(_this, '确认中...');
          setTimeout((data) => {
            // 结束动画
            comTools.hideLoading(_this);
            if (true) {
              _this.$vux.toast.show({
                type: 'success',
                text: '确认成功',
                width: '7.5em'
              });
              _this.$router.push({ path: '/mainpage/self/evaluate' });
            }
          })
        }
      });
    },
    // 去评价
    sendEvaluateFn () {
      // 评价详情页面跳转
      let orderId = this.$route.params.orderID;
      let value = {
        orderId: orderId,
        productList: this.orderDetail.productList
      }
      this.$store.commit('setData', { key: 'orderObj', value: value });
      this.$router.push({ path: '/mainpage/self/evaluate' });
    },
    // 退款售后
    sendRefundFn () {
      let orderId = this.$route.params.orderID;
      alert('退款id:'+orderId)
    },
  },
  created() {
    let self = this;
    let orderID  = this.$route.params.orderID;
    let orderList = this.$store.state.allOrder;
    // console.log(orderList);
    // 数据加载
    comTools.showLoading(this, '载入中...');
    setTimeout(() => {
      // 数据加载结束动画
      orderList.forEach((element) => {
        if (element.id == orderID) {
          self.orderDetail = element;
        }
      });
      comTools.hideLoading(this);
    }, 500)
  },  
  mounted () {

  },
  watch: {
    
  },
  // computed: {
  //   myfixed: function(){
  //     let value = this.orderDetail.payAccount;
  //     return value.toFixed(2);
  //   }
  // }
}



